@import url('./assets/scss/Reset.scss');

@import url('./assets/fonts/Open\ Sans/stylesheet.css');
@import url('./assets/scss/Colors.scss');
@import url('./assets/scss/CommonStyle.scss');
@import url('./assets/scss/Alertify.scss');
@import url('./assets/scss/TransitionGroup.scss');

@import './assets/scss/Colors.scss';

body {
	font-family: 'Open Sans';
	box-sizing: border-box;
}

.app-content {
	color: white;
	display: flex;
	justify-content: center;
	min-height: 100vh;
	height: 100%;
	background: rgb(239, 67, 219);
	position: relative;
}

::-webkit-scrollbar {
	width: 5px;
	height: 5px;
	margin-left: 5px;
}

/* Track */
::-webkit-scrollbar-track {
	background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: #1e1f2044;
	border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	opacity: .5;
}